import React from 'react';
import { Card, CardContent, Typography, Grid } from '@material-ui/core'
import CountUp from 'react-countup'
import styles from './Cards.module.css'
import cx from 'classnames'


const Cards = ({data: { confirmed, recovered, deaths, lastUpdate}}) => { 
	if(!confirmed){
		return 'Loading.....'
	}
	return (
		
		<div className={styles.container}>
		
		<Grid className={styles.container1} container spacing={3} justify="center">

			<Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.infected)}>
				<CardContent>
					<Typography gutterBottom>Infected</Typography>
					<Typography variant="h5">
						<CountUp
						className={styles.font}
							start={0}
							end={confirmed.value}
							duration={2.5}
							separator=","
						/>
					</Typography>
					<Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
					<Typography variant="body2">Infected people from COVID-19</Typography>

				</CardContent>
			</Grid>
			
			
			<Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.recovered)}>
				<CardContent>
					<Typography gutterBottom>Recovered</Typography>
					<Typography variant="h5">
						<CountUp
						className={styles.font}
							start={0}
							end={recovered.value}
							duration={2.5}
							separator=","
						/>
					</Typography>
					<Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
					
					<Typography variant="body2">{Math.round((recovered.value/confirmed.value)*100)}% Recovered out of total Infected</Typography>
				</CardContent>
			</Grid>
			
			
			<Grid item xs={12} md={3} component={Card} className={cx(styles.card, styles.deaths)}>
				<CardContent>
					<Typography gutterBottom>Deaths</Typography>
					<Typography variant="h5">
						<CountUp
						className={styles.font}
							start={0}
							end={deaths.value}
							duration={2.5}
							separator=","
						/>
					</Typography>
					<Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
					
					<Typography variant="body2">{Math.round((deaths.value/confirmed.value)*100)}% Died out of total Infected</Typography>

				</CardContent>
			</Grid>
			
		</Grid>
		
		</div>
		
		)
}
export default Cards;