import React,{ useState, useEffect } from 'react'
import { fetchCountries } from '../../api'
import { FormControl, NativeSelect, Typography } from '@material-ui/core'
import FadeIn from 'react-fade-in';


const CountryPicker =({handleChange})=>{
	const [countries, setCountries] = useState([])

	useEffect(() => {
		const fetchApiCount = async () => {
		 
			setCountries(await fetchCountries())
		}
		fetchApiCount()		
	},[setCountries])

	//console.log(countries)

 	const op = countries.map((co, i) => <option key={i} value={co}>{co}</option>)
 
	return(
		

		<FormControl>
	<Typography variant="subtitle1">Select Country:</Typography>
		<NativeSelect defaultValue="" onChange={(e) => handleChange(e.target.value)}>
		<option value="">Global</option>
			{op}
		</NativeSelect>
		</FormControl>
	
		)
}

export default CountryPicker